\n    font-weight: 300;\r\n    font-style: normal;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-LightItalic.otf\") format(\"truetype\");\r\n    font-weight: 300;\r\n    font-style: italic;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-Regular.otf\") format(\"truetype\");\r\n    font-weight: 400;\r\n    font-style: normal;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-Italic.otf\") format(\"truetype\");\r\n    font-weight: 400;\r\n    font-style: italic;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-Medium.otf\") format(\"truetype\");\r\n    font-weight: 500;\r\n    font-style: normal;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-MediumItalic.otf\") format(\"truetype\");\r\n    font-weight: 500;\r\n    font-style: italic;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-SemiBold.otf\") format(\"truetype\");\r\n    font-weight: 600;\r\n    font-style: normal;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-SemiBoldItalic.otf\") format(\"truetype\");\r\n    font-weight: 600;\r\n    font-style: italic;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-Bold.otf\") format(\"truetype\");\r\n    font-weight: 700;\r\n    font-style: normal;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-ExtraBold.otf\") format(\"truetype\");\r\n    font-weight: 800;\r\n    font-style: normal;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-ExtraBoldItalic.otf\") format(\"truetype\");\r\n    font-weight: 800;\r\n    font-style: italic;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-Black.otf\") format(\"truetype\");\r\n    font-weight: 900;\r\n    font-style: normal;\r\n}\r\n\r\n@font-face {\r\n    font-family: \"Evo\";\r\n    src: url(\"/fonts/OTF/Exo2.0-BlackItalic.otf\") format(\"truetype\");\r\n    font-weight: 900;\r\n    font-style: italic;\r\n}\r\n","/* --- Settings --- */\r\n@import \"./fonts.scss\";\r\n\r\n//? fonts\r\n$evo: \"Evo\";\r\n\r\n//? colors\r\n$white: #fff;\r\n$bright-grey: #bbb;\r\n$grey: #3a3a3a;\r\n$mid-grey: #282828;\r\n$dark-grey: #0e0e0e;\r\n$green: #1eb064;\r\n$violet: #a257d4;\r\n$red: #f63b28;\r\n$black: #000;\r\n$button-red: $red;\r\n$button-orange: #f85325;\r\n$button-grey: #464646;\r\n\r\n//? color of element\r\n$background-color: $grey;\r\n$header-background: linear-gradient(0deg, $dark-grey, $mid-grey);\r\n$dark-button-background: $button-grey;\r\n$color-button-background: linear-gradient(90deg, $button-red, $button-orange);\r\n$font-color: $white;\r\n$h1-color: $button-red;\r\n$footer-background: $button-grey;\r\n\r\n//? font size\r\n$h1-font-size: 36px;\r\n$h2-font-size: 42px;\r\n$h3-font-size: 28px;\r\n$h4-font-size: 24px;\r\n$p-font-size: 20px;\r\n\r\n//? font faimly\r\n$font-family: $evo, Arial, Helvetica, sans-serif;\r\n\r\n//? elements\r\n$container-width: 1100px;\r\n$container-margin-top: 0;\r\n$containter-padding: 25px;\r\n\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n  // overflow-x: hidden;\r\n}\r\n\r\nbody {\r\n  font-family: $font-family;\r\n  background: $background-color;\r\n  color: $font-color;\r\n  max-width: 100vw;\r\n\r\n  h2 {\r\n    font-size: $h2-font-size;\r\n  }\r\n\r\n  h3 {\r\n    font-size: $h3-font-size;\r\n  }\r\n\r\n  h4 {\r\n    font-size: $h4-font-size;\r\n  }\r\n\r\n  p {\r\n    font-size: $p-font-size;\r\n    color: $bright-grey;\r\n  }\r\n\r\n  a {\r\n    color: $red;\r\n    text-decoration: none;\r\n  }\r\n}\r\n\r\nheader {\r\n  position: relative;\r\n  .logo {\r\n    position: absolute;\r\n    left: $containter-padding;\r\n    top: $containter-padding;\r\n    height: 28px;\r\n  }\r\n  nav {\r\n    display: none;\r\n  }\r\n}\r\nmain {\r\n  .header {\r\n    background: $header-background;\r\n    padding: $containter-padding;\r\n    padding-top: 125px;\r\n    text-align: center;\r\n    h1 {\r\n      text-transform: uppercase;\r\n      font-style: italic;\r\n      margin-bottom: 25px;\r\n      font-size: $h2-font-size;\r\n\r\n      .main__header__h2 {\r\n        display: block;\r\n        color: $red;\r\n        font-weight: 600;\r\n      }\r\n    }\r\n    .begin {\r\n      margin-bottom: 100px;\r\n    }\r\n    .login {\r\n      position: relative;\r\n      h3 {\r\n        font-weight: normal;\r\n        margin-bottom: 20px;\r\n      }\r\n      form {\r\n        margin-bottom: 50px;\r\n        #login__login,\r\n        #login__password {\r\n          display: block;\r\n          margin: auto;\r\n          background-color: transparent;\r\n          border: 0;\r\n          border-bottom: 2px solid $white;\r\n          width: 280px;\r\n          height: 50px;\r\n          margin-bottom: 25px;\r\n          font-size: $p-font-size;\r\n          font-style: italic;\r\n          color: $white;\r\n        }\r\n        .form__wrapper {\r\n          display: flex;\r\n          justify-content: center;\r\n          margin-top: 40px;\r\n          #login__submit,\r\n          #login__sign_up {\r\n            border: 0;\r\n            background-color: $button-grey;\r\n            font-size: 18px;\r\n            font-family: $font-family;\r\n            color: $white;\r\n            width: 145px;\r\n            height: 50px;\r\n            border-radius: 20px;\r\n          }\r\n          #login__sign_up {\r\n            display: inline-block;\r\n            background: linear-gradient(90deg, $button-red, $button-orange);\r\n            width: 130px;\r\n            line-height: 50px;\r\n            margin-left: 25px;\r\n          }\r\n        }\r\n        #login__message {\r\n          color: $red;\r\n        }\r\n      }\r\n      .login__another_login__wrapper {\r\n    